<!--  -->
<template>
<div>


   <div id="farmtotel"></div>
</div>
</template>

<script>
export default {
data() {
return {

}
},
methods:{
     
      drawLine() {
        
          this.$axios.get("../../../static/json.json")
          .then(res=>{  
            console.log(res)
            let myChart = this.$echarts.init(document.getElementById("farmtotel"));
            this.$echarts.registerMap('henan', res.data);
              var option = {
              tooltip : {
                  trigger: 'item',
                  itemSize:'.12rem',
                  formatter:function(params){
                    var res=params.name+'<br />';
                        var myseries=option.series;
                        for(var i=0;i<myseries.length;i++){
                            //在内部继续循环series[i],从data中判断：当地区名称等于params.name的时候就将当前数据和名称添加到res中供显示
                            for(var k=0;k<myseries[i].data.length;k++){
                                // console.log(myseries[i].data[k].name);
                                //如果data数据中的name和地区名称一样
                                if(myseries[i].data[k].name==params.name){
                                    //将series数据系列每一项中的name和数据系列中当前地区的数据添加到res中
                                    // console.log(myseries)
                                    res+='总值'+':'+myseries[i].data[k].value+'<br />';
                                }
                            }
                        }
                        return res;

                  }
                  
              },
              dataRange: {
                 show:false,
                  x: 'left',
                  y: 'center',
                  splitList: [
                            {start: 1, end: 1,name:'北京市', color: '#0f2d3a'},
                            {start: 2, end: 2,name:'河南省', color: '#0f2d3a'},
                            {start: 3, end: 3,name:'河北省', color: '#0f2d3a'},
                            {start: 4, end: 4,name:'天津市', color: '#0f2d3a'},
                            {start: 5, end: 5,name:'内蒙古自治区', color: '#0f2d3a'},
                            {start: 6, end: 6,name:'甘肃省', color: '#0f2d3a'},
                            {start: 7, end: 7,name:'青海省', color: '#0f2d3a'},
                            {start: 8, end: 8,name:'西藏自治区', color: '#0f2d3a'},
                            {start: 9, end: 9,name:'宁夏回族自治区', color: '#0f2d3a'},
                            {start: 10, end: 10,name:'四川省', color: '#0f2d3a'},
                            {start: 11, end: 11,name:'山西省', color: '#0f2d3a'},
                            {start: 12, end: 12,name:'黑龙江省', color: '#0f2d3a'},
                            {start: 13, end: 13,name:'吉林省', color: '#0f2d3a'},
                            {start: 14, end: 14,name:'辽宁省', color: '#0f2d3a'},
                            {start: 15, end: 15,name:'山东省', color: '#0f2d3a'},
                            {start: 16, end: 16,name:'周口市', color: '#0f2d3a'},
                            {start: 17, end: 17,name:'江苏省', color: '#0f2d3a'},
                            {start: 18, end: 18,name:'重庆市', color: '#0f2d3a'},
                            {start: 19, end: 19,name:'湖北省', color: '#0f2d3a'},
                            {start: 20, end: 20,name:'云南省', color: '#0f2d3a'},
                            {start: 21, end: 21,name:'安徽省', color: '#0f2d3a'},
                            {start: 22, end: 22,name:'贵州省', color: '#0f2d3a'},
                            {start: 23, end: 23,name:'湖南省', color: '#0f2d3a'},
                            {start: 24, end: 24,name:'江西省', color: '#0f2d3a'},
                            {start: 25, end: 25,name:'浙江省', color: '#0f2d3a'},
                            {start: 26, end: 26,name:'广西壮族自治区', color: '#0f2d3a'},
                            {start: 27, end: 27,name:'福建省', color: '#0f2d3a'},
                            {start: 28, end: 28,name:'广东省', color: '#0f2d3a'},
                            {start: 29, end: 20,name:'台湾省', color: '#0f2d3a'},
                            {start: 30, end: 30,name:'海南省', color: '#0f2d3a'},
                            {start: 30, end: 30,name:'香港特别行政区', color: '#0f2d3a'},
                            {start: 30, end: 30,name:'澳门特别行政区', color: '#0f2d3a'},
                            
                  ],
                  textStyle: {
                  color: '#3899FF' // 值域文字颜色
                  },
                  selectedMode: true,
                  color: ['#E0022B', '#E09107', '#A3E00B']
                  },


              series: [
                {
                  type: 'map',
                  mapType: 'henan',//地图名称
                  itemStyle:{
                      normal:{label:{show:true}},
                      emphasis:{label:{show:true}},
                  },
                  label:{
                      normal:{
                        textStyle:{
                          color:'#fff'
                        }
                            }
                            
                        },
                  aspectScale: 0.75,
                  zoom: 1.2,
                  itemStyle: {
                      normal: {
                            borderWidth: 1, //区域边框宽度
                            borderColor: '#009fe8', //区域边框颜色
                            areaColor: "#ffefd5", //区域颜色
                            label: {
                        show: true//显示名称
                    }
                        },
                      
                        },
                      data:[
                            {name:'北京市', value:1},
                            {name:'河南省', value:2},
                            {name:'河北省', value:3},
                            {name:'天津市', value:4},
                            {name:'内蒙古自治区', value:5},
                            {name:'甘肃省', value:6},
                            {name:'青海省', value:7},
                            {name:'西藏自治区', value:8},
                            {name:'宁夏回族自治区', value:9},
                            {name:'四川省', value:10},
                            {name:'陕西省', value:11},
                            {name:'山西省', value:12},
                            {name:'黑龙江省', value:13},
                            {name:'吉林省', value:14},
                            {name:'辽宁省', value:15},
                            {name:'山东省', value:16},
                            {name:'江苏省', value:17},
                            {name:'新疆维吾尔自治区', value:18},
                            {name:'重庆市', value:18},
                            {name:'湖北省', value:18},
                            {name:'云南省', value:18},
                            {name:'安徽省', value:18},
                            {name:'贵州省', value:18},
                            {name:'湖南省', value:18},
                            {name:'江西省', value:18},
                            {name:'浙江省', value:18},
                            {name:'广西壮族自治区', value:18},
                            {name:'福建省', value:18},
                            {name:'广东省', value:18},
                            {name:'浙江省', value:18},
                            {name:'海南省', value:18},
                            {name:'台湾省', value:18},
                            {name:'香港特别行政区', value:18},
                            {name:'澳门特别行政区', value:18},
                            
                            ]
                }
             ]}
             myChart.setOption(option);
             window.addEventListener("resize",()=>{
               myChart.resize();
        });
            
          })
    
     
      }
},

created(){
  this.drawLine()
},
mounted() {
 
}
}
</script>
<style scoped>
/* @import url(); 引入css类 */
#farmtotel{
  width: 10rem;
  height: 10rem;
  margin: .2625rem 0 .2625rem -1.45rem;
}
</style>


